import { Card, CardActionArea, CardContent, CardMedia, Fab, Grid, Paper } from '@material-ui/core';
import { useFetch } from '../../hooks/'
import AddIcon from '@material-ui/icons/Add'

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(7),
    right: theme.spacing(7),
  },
  paper: {
    height: 345,
    width: 345,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export function Matrix(props) {
  const classes = useStyles();
  const [profileToken, setProfileToken] = useGlobal('profileToken')
  
  

  function execute() {
    return window.gapi.client.drive.files.list({})
        .then(function(response) {
          // Handle the results here (response.result has the parsed body).
          response.result.files.map(file => {
            if (file.name === '23407 matrix') {
              console.log(file)
            }            
          })
  
        },
        function(err) { console.error("Execute error", err); });
  }

  
  if (profileToken) {
    execute()
    return (<>hey</>)
  }
  return (<>no</>)
  
}