import React, { useEffect, useState, useGlobal, useRef } from 'reactn'
import { makeStyles, useTheme } from '@material-ui/core/styles'
import { AppBar, Avatar, Button, CssBaseline, Divider, Drawer, IconButton, List, ListItem, ListItemIcon, ListItemText, Toolbar, Typography } from '@material-ui/core'
import clsx from 'clsx';
import { ChevronLeft, ChevronRight, Creation, Inbox, Mail, Menu, Post } from 'mdi-material-ui';
import { Login } from './Login/'
import { Comms } from '../Comms/'
import { Matrix } from '../Matrix/'

const drawerWidth = 240;
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    boxShadow: theme.shadows[6],
    backgroundColor: theme.palette.common.white
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  brandText: {
    flexGrow: 1,
    fontFamily: 'monospace',
    whiteSpace: 'pre'
  },
  content: {
    flexGrow: 1,
    marginLeft: theme.spacing(8),
    marginTop: theme.spacing(7),    
    padding: theme.spacing(3)
  },  
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9) + 1,
    },
  },
  hide: {
    display: 'none',
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  spacer: {
    marginTop: theme.spacing(7)
  },
  title: {
    flexGrow: 1,
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },  
}));

function MiniDrawer() {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = useState(false);    
  const [sector, setSector] = useState('');

  const [profileToken, setProfileToken] = useGlobal('profileToken')
  const handleDrawerOpen = () => { setOpen(true) }
  const handleDrawerClose = () => { setOpen(false) }  
  

  function Options() {
    if (profileToken) {
      return (<>
      <List>
        <ListItem button key='post' onClick={() => setSector('comms')}>
          <ListItemIcon><Post/></ListItemIcon>
          <ListItemText primary="Comms" />
        </ListItem>
      </List>        
      <Divider />
      <List>
        <ListItem button key='matrix' onClick={() => setSector('matrix')}>
          <ListItemIcon><Creation/></ListItemIcon>
          <ListItemText primary="Matrix" />
        </ListItem>
      </List>
      </>);
    }
    return <></>;
  }

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            color="primary"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open,
            })}
          >
            <Menu />
          </IconButton>
          <Typography
              className={classes.brandText}
              color="primary">       
               __    _   ___/   ___/               _                         <br/>
               |\   |  .'  /\ .'  /\   ____ \,___, /        ___  .___    ___ <br/>
               | \  |  |  / | |  / |  (     |    \ |,---. .'   ` /   \ .'   `<br/>
               |  \ |  |,'  | |,'  |  `--.  |    | |'   ` |----' |   ' |----'<br/>
               |   \|  /`---' /`---' \___.' |`---' /    | `.___, /     `.___,<br/>
                                            \                                 
          </Typography>                                                                                                                                                 
          <Login />  
          {/* <List />       */}
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        <div className={classes.toolbar}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'rtl' ? <ChevronRight /> : <ChevronLeft />}
          </IconButton>
        </div>
        <Divider />
        <div className={classes.spacer} />
        <Divider />
        <Options />
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        
          {sector === 'comms' && <Comms />} 
          {sector === 'matrix' && <Matrix />}
        {/* <Typography paragraph>
          Consequat mauris nunc congue nisi vitae suscipit. Fringilla est ullamcorper eget nulla
          facilisi etiam dignissim diam. Pulvinar elementum integer enim neque volutpat ac
          tincidunt. Ornare suspendisse sed nisi lacus sed viverra tellus. Purus sit amet volutpat
          consequat mauris. Elementum eu facilisis sed odio morbi. Euismod lacinia at quis risus sed
          vulputate odio. Morbi tincidunt ornare massa eget egestas purus viverra accumsan in. In
          hendrerit gravida rutrum quisque non tellus orci ac. Pellentesque nec nam aliquam sem et
          tortor. Habitant morbi tristique senectus et. Adipiscing elit duis tristique sollicitudin
          nibh sit. Ornare aenean euismod elementum nisi quis eleifend. Commodo viverra maecenas
          accumsan lacus vel facilisis. Nulla posuere sollicitudin aliquam ultrices sagittis orci a.
        </Typography> */}
      </main>
    </div>
  );
}

export function Terminus(props) {
    return (
        // <ButtonAppBar />
        <MiniDrawer />
    )
}
